.virtual{
    margin-top: 6rem;
    margin-bottom: 6rem;
    padding: 2rem;
    display: flex;
    justify-content: space-around;
}

.left{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: relative;
}

.left>:first-child{
    font-size: 2rem;
    display: block;
    width: 20rem;
    font-weight: bold;
    text-transform: uppercase;
}
.left>:nth-child(2){
    font-size: 1.4rem;
    display: block;
    width: 20rem;
}
.left>:nth-child(3){
    font-size: 1.4rem;
    display: block;
    width: 20rem;
    text-transform: uppercase;
    font-weight: bold;
}

.left>img{
    position: absolute;
    width: 10rem;
    top: 55%;
    bottom: 0;
}

.wrapper{
    width: 30rem;
    border-radius: 10px;
}
.wrapper .rightImage{
    border-radius: 10px;
}

@media screen and (max-width:856px) {
    .virtual{
        gap: 1rem;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
        margin-bottom: 2rem;
        padding: 1rem;
        text-align: center;
    }
    .left>img{
        top: 65%;
    }
}
@media screen and (max-width:640px) {
    .wrapper{
        width: 20rem;
    }
}